@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;1,300&display=swap);

.navbar__list {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 0.5rem;
}

.navbar__item {
  color: #FFFBFB;
  text-decoration: none;
  font-size: larger;
  padding: 0.3rem 0.5rem;
}

.active {
  background-color: rgb(243, 240, 240);
  color: rgb(59, 56, 56);
  border-radius: 4px;
}

.home__main {
  padding-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

.home__image {
  width: 55vw;
}

@media only screen and (min-width: 420px) {
  .home__image {
    width: 17vw;
  }
}

.home__name {
  padding-top: 2rem;
  font-weight: normal;
}

.home__signature {
  padding-top: 0.5rem;
  font-weight: normal;
}

.home__desc {
  padding: 3rem 4rem 0;
  text-align: center;
  font-weight: 300;
  font-style: italic;
  font-size: 18px;
}

.home__moto {
  padding: 3rem 5rem 0;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
}

.arrow-down {
  margin-top: 4rem;
}
.about__main {
  border-top: 1px solid rgb(243, 240, 240);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

p {
  padding: 0.5rem 3rem;
  font-weight: 300;
  font-style: italic;
  font-size: 18px;
}

.main__attributes {
  display: flex;
  padding: 2rem 3rem 0;
}

.attributes__left {
  font-weight: 500;
  font-style: normal;
  list-style-type: none;
  padding-right: 1rem;
}

.attributes__left li {
  margin-bottom: 0.5rem; 
}

.attributes__right {
  font-weight: 300;
  font-style: italic;
  list-style-type: none;
}

.attributes__right li {
  margin-bottom: 0.5rem; 
}

.arrow-up {
  margin-bottom: 2rem;
}

.arrow-down {
  margin-top: 2rem;
}
.projects {
  border-top: 1px solid rgb(243, 240, 240);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.projects__main {
  width: 80vw;
  background-color: #FFFBFB;
  display: flex;
  margin: auto;
  border-radius: 4px;
  margin-top: 2rem;
}

@media only screen and (min-width: 420px) {
  .projects__main {
    width: 55%;
    background-color: #FFFBFB;
    display: flex;
    margin: auto;
    border-radius: 4px;
    margin-top: 2rem;
  }
}

.main__image {
  width: 100px;
  height: 200px;
  margin: 0;
  border-radius: 4px;
}

.main__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: space-around;
  width: 100%;
}

.text__title {
  color: #323232;
  text-shadow: none;
}

.text__subtitle {
  color: #323232;
  font-style: italic;
  margin: 0 0.5rem;
  text-shadow: none;
}

.text__buttons {
  display: flex;
  justify-content:space-evenly;
  width: 100%;
}

.button {
  text-decoration: none;
  background-color: #323232;
  padding: 0.5rem;
  border-radius: 4px;
  box-shadow: #323232;
}

.button:hover {
  background-color: #4b4a4a;
}

.arrow-up {
  margin-bottom: 4rem;
}

.arrow-down {
  margin-top: 4rem;
}
.contact {
  border-top: 1px solid rgb(243, 240, 240);
  padding-top: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.main__touch {
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  padding: 2rem 0;
}

.main__find, .main__email, .main__talk {
  font-style: italic;
  font-weight: 500;
  font-size: 22px;
  padding-bottom:1rem;
}

.socials {
  width: 70vw;
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 4rem;
}

.phone, .email {
  width: 70vw;
  display: flex;
  justify-content: center;
  padding-bottom: 4rem;
}

@media only screen and (min-width: 420px) {
  .email, .phone, .socials {
    width: 40%;
  }
}

.footer {
  border-top: 1px solid rgb(243, 240, 240);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #323232;
}

.footer p {
  padding: 0.5rem;
  font-size: 12px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  color: #FFFBFB;
  text-shadow: 2px 2px 4px #242323;
}

body {
  background-color: #5d5c5c;
}

html {
  scroll-behavior: smooth;
}

#root {
  max-width: 800px;
  margin: auto;
  background-color: #323232;
}
